module.exports = {
  init: function init() {
    var codeToRunOnClient = "\n      (function() {\n        function getInitialColorMode() {\n                const persistedColorPreference = window.localStorage.getItem('nightwind-mode');\n                const hasPersistedPreference = typeof persistedColorPreference === 'string';\n                if (hasPersistedPreference) {\n                  return persistedColorPreference;\n                }\n                const mql = window.matchMedia('(prefers-color-scheme: dark)');\n                const hasMediaQueryPreference = typeof mql.matches === 'boolean';\n                if (hasMediaQueryPreference) {\n                  return mql.matches ? 'dark' : 'light';\n                }\n                return 'light';\n        }\n        getInitialColorMode() == 'light' ? document.documentElement.classList.remove('dark') : document.documentElement.classList.add('dark');\n      })()\n    ";
    return codeToRunOnClient;
  },
  beforeTransition: function beforeTransition() {
    var doc = document.documentElement;

    var onTransitionDone = function onTransitionDone() {
      doc.classList.remove('nightwind');
      doc.removeEventListener('transitionend', onTransitionDone);
    };

    doc.addEventListener('transitionend', onTransitionDone);

    if (!doc.classList.contains('nightwind')) {
      doc.classList.add('nightwind');
    }
  },
  toggle: function toggle() {
    module.exports.beforeTransition();

    if (!document.documentElement.classList.contains('dark')) {
      document.documentElement.classList.add('dark');
      window.localStorage.setItem('nightwind-mode', 'dark');
    } else {
      document.documentElement.classList.remove('dark');
      window.localStorage.setItem('nightwind-mode', 'light');
    }
  },
  enable: function enable(dark) {
    var mode = dark ? "dark" : "light";
    var opposite = dark ? "light" : "dark";
    module.exports.beforeTransition();

    if (document.documentElement.classList.contains(opposite)) {
      document.documentElement.classList.remove(opposite);
    }

    document.documentElement.classList.add(mode);
    window.localStorage.setItem('nightwind-mode', mode);
  },
  // Old
  checkNightMode: function checkNightMode() {
    return window.matchMedia && window.matchMedia('(prefers-color-scheme: dark)').matches;
  },
  watchNightMode: function watchNightMode() {
    if (!window.matchMedia) return;
    window.matchMedia('(prefers-color-scheme: dark)').addListener(module.exports.addNightModeSelector());
  },
  addNightModeSelector: function addNightModeSelector() {
    if (module.exports.checkNightMode()) {
      document.documentElement.classList.add('dark');
    } else {
      document.documentElement.classList.remove('dark');
    }
  },
  addNightTransitions: function addNightTransitions() {
    if (!document.documentElement.classList.contains('nightwind')) {
      document.documentElement.classList.add('nightwind');
    }
  },
  initNightwind: function initNightwind() {
    module.exports.watchNightMode();
    module.exports.addNightModeSelector();
    module.exports.addNightTransitions();
  },
  toggleNightMode: function toggleNightMode() {
    if (!document.documentElement.classList.contains('dark')) {
      document.documentElement.classList.add('dark');
      window.localStorage.setItem('nightwind-mode', 'dark');
    } else {
      document.documentElement.classList.remove('dark');
      window.localStorage.setItem('nightwind-mode', 'light');
    }
  }
};